module.exports = {"t1":"Mit der Kraft des Windes","t2":"... entwickelt sich unsere Gesellschaft. Wir bei Green Wind Energy tragen zur Gestaltung und Sicherung unser aller Zukunft bei. Ich bin fasziniert von der Kraft des Windes!","t3":"Martin Khl","t4":"Geschftsfhrung\nseit 1995 in Sachen Windenergie unterwegs","t5":"Green Wind Energy","t6":"... ist ein Unternehmen der Windenergie mit Sitz in Berlin-Mitte und Projekten in ganz Deutschland. Wir agieren selbstndig und unabhngig. Wir geben volle Untersttzung in der Projektentwicklung von Windenergieanlagen fr private und institutionelle Windparkbetreiber. Wir arbeiten teamorientiert und partnerschaftlich und sind seit vielen Jahren fest verankert in der Windenergie.","b1":"Leistungen","t7":"Frisches Windwissen","t8":"Bei Green Wind Energy paart sich frisches Windwissen mit gereifter Erfahrung. Die berzeugung, jede Hrde zu nehmen, verbindet sich mit Begeisterung, wenn sich die Rotoren reibungslos drehen.","b2":"Referenzen","t9":"Das Beste fr Sie","t10":"Wir behaupten nicht, dass wir alles besser knnen. Aber wir sagen, dass wir das Allerbeste fr unsere Kunden herausholen - aus Maschinen, Technologien, Vertrgen, Gesetzen und aus uns. Unser Team verfgt ber die Kapazitten und Kompetenzen, ein Windprojekt von der Flchenidentifizierung ber die Finanzierung bis hin zum Bau und der Inbetriebnahme zu betreuen und mageschneiderte Lsungen sowie Planungssicherheit zu garantieren. Dabei gehrt auch das Repowering, die Erneuerung alter Anlagen, zu unseren Aufgaben.","b3":"Team","t11":"","t12":"","t13":"","t14":"","t15":"","t16":"","t17":"","t18":"","t19":""}

