module.exports = {"t1":"Effizienz und Engagement vor Ort","t2":"Green Wind Energy entwickelt in der und fr die Region Windenergie-Projekte vor Ort. Wir sind der Ansprechpartner, wenn es um die Verdichtung von bestehenden Parks, um Repowering oder auch um Neu-Entwicklungen mit Kooperationspartnern geht. Sehen Sie hier eines der grten bundesdeutschen Repoweringprojekte 2017 und weitere beispielhafte Projekte in Brandenburg.","sections":[{"title":"Windpark Repowering","subsections":[{"title":"Projekt Vormark/Prignitz (Gro Pankow)","text":"Green Wind Energy baute Ende 2016 im Windeignungsgebiet 14 bestehende Alt-Anlagen zurck. An gleicher Stelle wurden 14 neue WEA errichtet, die im ersten Quartal 2017 in Betrieb genommen wurden, inkl. eines eigenen Umspannwerkes in Kuhsdorf zum Anschluss der Windenergieanlagen.\n\nNennleistung 48,3 Megawatt\n\nDie Errichtung der neuen WEA mit einer Nabenhhe von 137 Metern und einer Nennleistung von je 3.450 Kilowatt (vorher 1.500 Kilowatt) erfolgte innerhalb von drei Monaten. Aus 21 Megawatt Nennleistung wurden damit 48,3 Megawatt. Der Energieertrag erhht sich durch das Repowering um ca. das 4-Fache. Der Windpark ist mit bedarfsgerechter Nachtkennzeichnung ausgestattet.\n\nAusgleichsmanahmen\n\nAls Ausgleichsmanahmen wurden auf Wunsch der Gemeinde nicht mehr bentigte Gebude abgerissen, Bume gepflanzt sowie eine Streuobstwiese angelegt. Zauneidechsen wurden in Ersatzhabitate umgesetzt. Die Fledermuse erhielten an drei Stellen Ersatzksten und fr die Rauchschwalben wurden Nester aufgehngt.\n\nProjektpartner: European Energy\nTechnische Betriebsfhrung: Green Wind Operations\n\n","img":"http://res.cloudinary.com/greenwind/image/upload/c_scale,w_800/v1505147660/GWE_Referenzen_037C9H1B2821_tujamd.jpg","video":"https://player.vimeo.com/video/233281688?color=000000&title=0&byline=0&portrait=0"},{"title":"Projekt Timpberg","text":"Standort: Zehdenick, Prignitz-Oberhavel, Brandenburg\nAnlagen alt: 2 x VESTAS V80, 4 MW Leistung, 78 m Nabenhhe, Inbetriebnahme 2002\nRckbau: 08/2015\nAnlagen neu: 2 x VESTAS V112, 6 MW Leistung, 125 m Nabenhhe, Inbetriebnahme 12/2015\nBest: profitables Exit fr den Eigentmer der alten Windenergieanlagen, attraktives Projekt fr den neuen Betreiber, bestndige Untersttzung durch die Bevlkerung, Green Wind Energy als Generalunternehmer","img":""}]},{"title":"Windpark Verdichtung","subsections":[{"title":"Projekt Vormark","text":"Erste Erweiterung\nStandort: Gro Pankow, Prignitz-Oberhavel, Brandenburg\nAnlagen: 2 x VESTAS V80, 4 MW Leistung, 78 m\nNabenhhe\nInbetriebnahme: 2011 erster Teil der mehrstufigen Neuplanung des gesamten Windparks, s.o.\nBest: starke private und ffentliche Untersttzung, Green Wind Energy als Generalunternehmer","img":""}]},{"title":"Kooperation Neuentwicklung","subsections":[{"title":"Projekt Klein-Mutz","text":"Standort: Zehdenick, Prignitz-Oberhavel, Brandenburg\nAnlagen: 4 x VESTAS V90, 8 MW Leistung, 125 m Nabenhhe\nInbetriebnahme: 12/2012\n\nProjektpartner: Saxovent, Notus\nBest: konstruktive Zusammenarbeit, kurze Projektlaufzeit fr Genehmigung und Realisierung","img":"http://res.cloudinary.com/greenwind/image/upload/c_scale,q_50,w_1052/v1505994982/AIMG_0794_gr_x1eggg.jpg"}]}]}

