module.exports = {"t1":"Windwissen","t2":"Wir sammeln fr Sie Windwissen: aktuelle News aus der Branche, neue gesetzliche Regelungen, Aspekte der politischen Diskussionen, Verbandsneuigkeiten oder auch interessante Links zu Windthemen. Wenn Sie mehr wissen wollen, kontaktieren Sie uns bitte.","sections":[{"title":"HUSUM Wind","subtitle":" von den Husumer Windenergietagen bis heute","text":"Als noch kaum jemand auf der Welt die Windenergie als Wirtschaftsfaktor ernst nahm, fanden 1989 die ersten 'Husumer Windenergietage' statt  in einer Viehauktionshalle. Schnell zeigte sich, dass die Stadt Husum an der windreichen Nordsee das Potenzial nicht nur zum fhrenden Wind-, sondern auch zum Messestandort hat. 1997 wurde eine neue Messehalle fertiggestellt und deren Infrastruktur in den Folgejahren stetig verbessert. Im August 2010 ffnete dann das modern und multifunktional ausgestattete NordseeCongressCentrum, welches fr die HUSUM WindEnergy 2010 erstmalig erfolgreich fr Tagungen und Kongresse genutzt wurde. Und der Messeplatz wchst weiter.","link":{"text":"Mehr unter","url":"www.husumwind.com"},"more":"Green Wind Operations GmbH ist vom 15. - 18. September 2015 dabei und freut sich auf Ihren Besuch."},{"title":"Neue Regelungen fr Betriebsfhrung","subtitle":"","text":"Systemstabilitt:\n Allgemein\n Systemstabilittsverordnung (seit Mrz 2015)\n\nPrfung von Serviceliften: Allgemein neue BetrSichV (seit 01. Juni 2015 in Kraft)\n\nISDN und analoge Anschlsse: Telekom kndigt Abschaltung zu 2016/2017 an.\n\nFernwirkanlagen: Austausch durch Netzbetreiber (EEG 2014, 9 Absatz 1)"},{"title":"Strom aus Wind","subtitle":"","text":"Stromerzeugung aus Windkraft hat innerhalb weniger Jahre stark zugenommen.Im Jahr 2014 produzierten in Deutschland 24.867 Windenergieanlagen mit einer installierten Leistung von gut 38.115 Megawatt sauberen Strom fr Unternehmen und Haushalte. Damit deckt die Windenergie heute 8,9 Prozent des gesamten Bruttostromverbrauchs und liefert den grten Beitrag zur Stromerzeugung aus Erneuerbaren Energien. Mehr als 137.000 Menschen sind in der Windenergiebranche beschftigt. Das Potenzial der Windenergie ist noch nicht ausgeschpft...","link":{"text":"Mehr unter ","url":"www.unendlich-viel-energie.de"}},{"title":"Blinken, wenn ein Flugzeug kommt","subtitle":"","text":"In Zukunft wird es zulssig sein, dass die blinkenden Kennzeichnungsleuchten auf den Anlagen nur bei Nherung von Luftfahrzeugen eingeschaltet werden. Die entsprechenden nderung der Verwaltungsvorschrift wurde am 10. Juli 2015 im Bundesrat angenommen. Sie sieht die Zulassung neuer Lichttechnologien, die Einfhrung von Obergrenzen fr die Lichtstrke, die Verringerung der Mindestanzahl der Hinderungsbefeuerungsebenen bei der Befeuerung von Windenergieanlagen sowie die Erffnung der Mglichkeit, die Nachtkennzeichnung von Windenergieanlagen bedarfsgesteuert zu aktivieren, vor.\n\nIn Deutschland mssen Windenergieanlagen mit einer Hhe von ber 100 Metern in der Nacht mit einem rot blinkenden Feuer gekennzeichnet werden. (...) Dass Dauerblinken knnte damit, zumindest bei greren Windparks, der Vergangenheit angehren. Entscheidend fr den Erfolg der bedarfsgerechten Kennzeichnung ist die Wirtschaftlichkeit der Systeme (...)","link":{"text":"Quelle: BWE, 10.7.15, ","url":"www.wind-energie.de"}}]}

