module.exports = {"t1":"Mit der Kraft des Windes","t2":"... arbeiten wir daran, dass Energie, die auf dem Meer gewonnen wird, effizient, reibungslos und kostengnstig ins Land strmt. Zur Verfgbarkeit aller! Jedes Mitglied unseres Teams hat einen hohen Anspruch an seine Profession und ist mit voller Kraft bei der Sache. Das macht uns stark!","t3":"Manuel Lasse, Dipl.-Ing. (FH)\nfr Umwelttechnik und Erneuerbare Energien","t4":"Geschftsfhrung\nseit 2006 mit voller Kraft voraus in Sachen Windenergie.","t5":"Green Wind Offshore","t6":"Die Green Wind Offshore GmbH ist ein Unternehmen der Green Wind Gruppe mit Sitz in Berlin-Mitte. Als Spezialist fr Instandhaltungsmanagement und berwachung technischer Anlagen zur Energieerzeugung Offshore sind wir zudem in Rostock und im Raum Emden/Wilhelmshaven ttig.\n\nWir agieren selbstndig und unabhngig fr den gesamten deutschen und internationalen Markt. Wir geben volle Untersttzung in der technischen Betriebsfhrung von Offshore-Windenergieanlagen und im Plattformmanagement fr HG und Umspannwerke. Wir arbeiten teamorientiert und partnerschaftlich und sind seit vielen Jahren fest verankert in der Windenergie. Zertifiziert nach ISO 9001:2015.","b1":"Leistungen","t7":"Frisches Windwissen","t8":"Green Wind Offshore ist frisches Windwissen, gepaart mit Erfahrung und der berzeugung, jede Welle zu nehmen. Das ist Begeisterung, wenn ein Rdchen ins andere passt, wenn sich Planen, Handeln und Organisieren zu einem groen Ganzen reibungslos zusammenfgen und all der geleistete Einsatz zu purer Energie wird.","b2":"Referenzen","t9":"Das Beste fr Sie","t10":"Wir behaupten nicht, dass wir alles besser knnen. Aber wir sagen, dass wir das Allerbeste fr unsere Kunden herausholen  aus Maschinen, Materialien, Technologien, Vertrgen, Gesetzen und aus uns. Wir sind engmaschig vernetzt und am Ort des Geschehens in den Nord- und Ostsee-Regionen. Unsere Kunden knnen sich auf kurze Wege und direkte Ansprechpartner verlassen.","b3":"Team"}

