module.exports = {"t1":"Unser Team\nVolle Kraft voraus!","t2":"Es gibt viele gute Grnde, sich in Sachen Technisches Management fr Green Wind Offshore zu entscheiden. Jedes Mitglied unseres Teams ist ein guter Grund. Und alle zusammen sind wir mehr als die Summe einzelner. Unsere Devise: Volle Kraft voraus!","team":[{"name":"Manuel Lasse","job":"Geschftsfhrer","bio":"seit 2006 mit voller Kraft voraus in Sachen Windenergie.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505142196/Manu_Lasse_250_wmmhuo.jpg"},{"name":"Peter-Heinrich Boysen","job":"Technischer Leiter","bio":"zielstrebig zu Wasser und zu Land.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505142196/Peter_Heinrich_Boysen_250_k7xei3.jpg"},{"name":"Andreas Texheimer","job":"Qualittsmanager","bio":"mit Genauigkeit und dem Fokus auf Safety first.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505141768/Andreas_Texheimer_250_nzbrbq.jpg"},{"name":"Rico Kaatz","job":"Projektingenieur CMMS","bio":"immer erneuerbar unterwegs.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505142196/Rico_Kaatz_250_jxgxfh.jpg"},{"name":"Fabio da Fonseca Bola","job":"Projektingenieur CMMS","bio":"frisches Windwissen direkt von der Hochschule.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505142196/Fabio_da_Fonseca_Bola_250_cy5eo9.jpg"}]}

