module.exports = {"t1":"Leitwarte 24/7","t2":"Effektiv und koordiniert berwachen.","t2b":"Das gesamte Projektmanagement von Offshore-Groprojekten wird im Wesentlichen in unseren Leitstellen 24/7 abgebildet.","sections0":[{"title":"Leitstellen nah dran","p":"Hauptleitwarte und Herz ist Berlin. Von hier aus setzen wir in unmittelbarer Nhe zu den Standorten der Projekte Leitstellen ein, wie z. B. beim Instandhaltungsmanagement des Netzanschlusssystems DolWin3. Ob Abfall- und Abwasserentsorgung, Ersatzteilversorgung und -management oder Technical Support Line (TSL)  hier dokumentieren, systematisieren, berwachen und kontrollieren unsere Experten den gesamten Anlagenbetrieb und -zustand."},{"title":"24/7 berwachung","p":"Alle Einsatzteams und deren Aktivitten knnen so optimal gesteuert und ber die vorhandene Lagerlogistik mit den bentigten Ersatzteilen und den entsprechenden Werkzeugen versorgt werden. Alle Kernprozesse werden zentral berwacht und koordiniert  24 Stunden am Tag, sieben Tage die Woche. Effektiv und sicher."}],"t3":"","t4":"Planung","t5":"Neue Konzepte denken.","t6":"Ausgangspunkt unseres technischen Managements ist der Leitsatz: Betriebszeit maximieren und Kosten reduzieren.\n\nInstandhaltungsmanagement und technische Betriebsfhrung haben knftig einen groen Anteil am wirtschaftlichen Erfolg von Offshore-Windparkprojekten.\n\nUnser ausgeklgeltes Konzept beginnt mit einer akribischen Vorbereitungsphase. In Abstimmung mit Auftraggebern, Werften, Konstrukteuren und Ingenieuren schaffen wir Planungssicherheit.","sections1":[{"title":"Vorausschauen und Systematisieren","p":"Zu unseren Strken gehren eine flexible und vorausschauende Denke, Genauigkeit in der Planung, bersicht in Kampagnen und Strukturen, 100-prozentige Dokumentation von Materialien und Vorgngen, systematische Untersttzung von Instandhaltungsablufen durch Software-Einsatz (CMMS)."},{"title":"Fr alle Offshore-Groprojekte","p":"Das von Green Wind Offshore neu entwickelte Instandhaltungskonzept fr Windenergieanlagen, Plattformen, Kabel zu Land und zu Wasser, Umspannwerke und Schaltanlagen kann auf Groprojekte in Nord- und Ostsee wie auch an den Ksten Englands und Frankreichs bertragen werden."}],"t9":"Management","t10":"","sections2":[{"title":"Schnell und direkt handeln.","p":"Unser Handeln ist bestimmt von kurzen Reaktionszeiten und dem Ziel, Stillstand zu vermeiden und die Effektivitt zu erhhen.\n\nDer Einsatz neuester Technik dient der maximalen Verfgbarkeit von Anlagen sowie hchsten Sicherheitsstandards fr Mensch und Anlage. Eine zertifizierte Offshore-Ausbildung unserer Mitarbeiter ist selbstverstndlich. Hohe Motivation und Leistungsbereitschaft potenziert Qualitt. Dazu zhlen auch: flache Hierarchien und schnelle Entscheidungsprozesse sowie Kooperationsbereitschaft und partnerschaftliches Handeln. Denn Offshore-Groprojekte brauchen keine Einzelkmpfer, sondern erstklassige Teamplayer."},{"title":"Zu Land und zu Wasser","p":"Zu unserem Spektrum zhlt die Organisation und Koordinierung smtlicher planbarer Instandhaltungsmanahmen, unplanmiger Instandsetzungsarbeiten und Streinstze sowie zustzlicher Instandhaltungsmanahmen zu Land und zu Wasser."},{"title":"Leitstellen und Logistikbasen","p":"Dank unserer Leitstellen, unserer Logistikbasen, der flexiblen Einsatzkrfte und leistungsstarken Partner hat Green Wind Offshore die Mglichkeit, weitere HG Plattformen, Offshore Umspannwerke und Offshore-Windenergieanlagen in vorhandene Instandhaltungs-Strukturen einzubinden und ggfs. zu erweitern. Wir integrieren Einzelsysteme und entlasten damit unsere Kunden."},{"title":"Managementsysteme","p":"Unsere wichtigsten Ziele im Bereich Qualittsmanagement sind eine strukturierte innere Organisation sowie eine kooperative und erfolgreiche Zusammenarbeit mit Kunden und Lieferanten. Dazu gehrt ebenfalls die Einhaltung der Termine und Kosten. Aber auch die Bereiche Sicherheit, Arbeitsschutz und Umwelt unterliegen der kontinuierlichen Kontrolle. Wir arbeiten kontinuierlich an allen Systemen, wie","list":["Qualittsmanagementsystem (QMS) nach ISO 9001","IT-Sicherheitsmanagementsystem nach ISO 27001","Umweltmanagementsystem (UMS) nach ISO 14001","Asset-Managementsystem nach ISO 55001","Arbeitsschutzmanagementsystem und HSE u. a. integriert in der 9001 (QMS) und 14001 (UMS) sowie OHSAS ISO 18001","Energiemanagementsystem nach ISO 50001"]}],"t11":"Logistik","t12":"","sections3":[{"title":"Flexibel und punktgenau organisieren.","p":"Fr hohe Verfgbarkeiten und optimierte Arbeitsablufe mssen Logistik und Ersatzteilversorgung im technischen Management zu jeder Zeit stimmen."},{"title":"Kstennahe Standorte","p":"Auf dem Wasser kann schon eine fehlende Schraube unntigen Aufwand bedeuten. Damit das Ersatzteilmanagement sowie die Ver- und Entsorgung von Verbrauchs- und Betriebsstoffen ohne Verzug gelingt, installieren und verwalten wir kstennahe Standorte inklusive Ersatzteillager  mit Manpower und per CMMS. Die sichere Offshore-Befrderung von Materialien und Personen und deren Unterbringung gehren ebenso zu unseren Aufgaben wie Transporte von Lagerstandorten zu Umschlaghfen und landseitigen Umspannwerken."},{"title":"Prventiv und korrektiv","p":"Jedes Groprojekt hat seine besonderen Anforderungen, auf die wir flexibel eingehen. Prventive und korrektive Instandhaltungskampagnen erfordern den punktgenauen Einsatz von Technikern zu Wasser und zu Land."}]}

