module.exports = {"t1":"Leitwarte 24/7","t2":"Die gesamten Ablufe der technischen Betriebsfhrung von Windenergieanlagen und Umspannwerken werden im Wesentlichen in unseren Leitstellen 24/7 abgebildet. Hauptleitwarte und Herz ist Berlin. Wir dokumentieren, systematisieren, berwachen und kontrollieren den gesamten Anlagenbetrieb und -zustand. Unsere Einsatzteams und deren Aktivitten knnen so optimal gesteuert und effizient am Ort des Geschehens eingesetzt werden. Alle Kernprozesse werden zentral berwacht und koordiniert  24 Stunden am Tag, sieben Tage die Woche.\n\nEffektiv und sicher.","t3":"","t4":"Technische Betriebsfhrung","t5":"","t6":"","sections1":[{"title":"Checken was luft","p":"Wir inspizieren regelmig die Anlagen - vom Fahrkorb bis zum Maschinenhaus, kontrollieren und dokumentieren minutis, ob Prfungsintervalle und Wartungsaufgaben eingehalten werden. Dazu gehrt auch die halbjhrliche Begehung der kompletten WEA. ber alle Vorgnge sind unsere Kunden immer im Bilde, dank kundeneigenem Webzugang, individueller Berichterstellung und persnlicher Berichterstattung."},{"title":"Genau auf den Punkt","p":"Erst, wenn die technische Arbeit vor Ort und die Auswertung von Daten, Gutachten und Inspektionen, das Einholen von Angeboten sowie die detaillierte Untersuchung eines Objektes zusammenkommen und kompetent verbunden werden, erzielen wir optimale Ergebnisse. Erst dann entwickelt eine Technische Betriebsfhrung ihre volle Kraft. In diesem Sinne koordiniert Green Wind Operations auch externe BetriebsfhrerInnen in ganz Deutschland und dem benachbarten Ausland, schafft einheitliche Strukturen und bndelt Informationsflsse."},{"title":"Rund um die Uhr berwacht","p":"Wir verwenden fr die Datenberwachung das bewhrte Programm ROTORSOFT der Firma Drehpunkt sowie von der Firma Softenergy die Software WIS, d. h.:\n\n 24 Stunden Live-berwachung der Windenergieanlagen\n Software gesttzte Schnittstelle zur kfm. Betriebsfhrung\n ideale Voraussetzung zum Stromhandel\n\nDie individuelle Berichterstellung ist abgestimmt auf Ihr Portfolio durch Monats und Jahresberichte. Zustzlich erstellen wir kfm. Betriebsfhrungsberichte."},{"title":"berwachung externer Dienste","p":"Vertrauen ist gut, Kontrolle ist besser! Die Arbeiten externer Dienstleister werden stetig kontrolliert, Vertragsabsprachen und Abrechnungen geprft. Wir organisieren und setzen Nebenbestimmungen gem BiMSchG um."}],"t9":"Kaufmnnische Betriebsfhrung","t10":"Kaufmnnische Betriebsfhrung bei Green Wind heit, eine stets flieende, finanzielle Bewegung zu garantieren, alle Strfaktoren und unntigen Kosten auszuschlieen, um den bestmglichen Ertrag herauszuholen. Schon im Vorfeld knnen wir das gesamte Portfolio im Hinblick auf bestehende Vertrge prfen und Vorschlge fr eine sinnvolle Vertragsstruktur unterbreiten, z.B. zur Optimierung von Wartungsvertrgen.","sections2":[{"title":"Fakten optimieren","p":"Das Herzstck einer Kaufmnnischen Betriebsfhrung ist eine hieb- und stichfeste Buchhaltung nebst Abwicklung des Zahlungsverkehrs. Darberhinaus optimiert unser Financial Management den buchhalterischen Prozess so, dass er auf den Betrieb jeder einzelnen Anlage zugeschnitten ist, angepasst an die spezifischen Finanzierungsstrukturen und die steuerlichen Gegebenheiten. Unsere MitarbeiterInnen errechnen die beste Anlagen-Effizienz und kmmern sich um eine reibungslose Abrechnung sowie Verteilung der Einspeiseerlse."},{"title":"Prozesse planen und steuern","p":"Zur Planung und Steuerung dieser Prozesse nutzen wir unser frisches Windwissen in Sachen Liquiditts- und Budgetplanung, Abrechnung von NSM-Ausfllen, Direktvermarktung, Commercial Due Diligence etc. Und alles basiert auf dem stetigen Kontakt zum Kunden."}],"t11":"Repowering vs. Optimierung","t12":"Die technologische Entwicklung von Windenergieanlagen hat in den letzten Jahren enorme Fortschritte gemacht  sowohl was die Effizienz, die Umweltvertrglichkeit als auch die Mglichkeit anbelangt, unterschiedlichste Windstandorte bestmglich zu nutzen. Unsere BetriebsfhrerInnen kennen die Anlagen genau und machen Vorschlge fr Betriebsoptimierungen oder auch Repoweringmanahmen.","sections3":[{"title":"Laufzeit weitere 20 Jahre","p":"Fr viele Windprojekte, die vor mehr als zehn Jahren erbaut wurden, lohnt sich ein Repowering - also der Ersatz einer lteren und kleineren Anlage durch eine neue. Die Laufzeit eines bestehenden Projektes kann somit um weitere 20 Jahre verlngert, die Effizienz um das drei- bis fnffache gesteigert werden. Da kann sich eine Investition schnell amortisieren."},{"title":"Lohnt sich Repowering?","p":"Wir sehen es als unsere Aufgabe an, unseren Kunden vorausschauend und aktiv Vorschlge zum Repowering zu machen und fachkundig zu ermitteln, ob sich ein Repowering tatschlich lohnt. Erst wenn alle Fakten auf dem Tisch liegen, muss sich der Betreiber entscheiden."},{"title":"Projektierung mit Partner","p":"Unsere Partnerfirma Green Wind Energy GmbH hilft bei der Umsetzung einer Repowering-Manahme  von der Voruntersuchung und den Genehmigungsverfahren ber die Umsetzung bis hin zum Betrieb. Green Wind Energy entwickelt seit Jahren mit Partnern Windparks in der Region und steuert Repowering-Prozesse. Wenn gewnscht, kann die Green Wind Operations GmbH anschlieend die technische und kaufmnnische Betriebsfhrung in gewohnter Qualitt nahtlos bernehmen."}]}

