module.exports = {"t1":"Med vindens kraft","t2":"... udvikles vores samfund. Hos Green Wind Energy bidrager vi til at forme og sikre hele vores fremtid. Jeg er fascineret af vindens kraft!","t3":"Martin Khl","t4":"Administrerende direktr\nshar arbejdet med vindenergi siden 1995","t5":"Green Wind Energy","t6":"... er en vindenergivirksomhed med hovedsde i Berlin centrum og projekter i hele Tyskland. Vi opererer selvstndigt og uafhngigt. Vi giver fuld sttte til projektudvikling af vindenergianlg til private og institutionelle operatrer af vindparker. Vi arbejder med fokus p samarbejde og partnerskab og har i mange r vret fast forankret i vindenergien. Certificeret iht.","b1":"Ydelser","t7":"Ny viden om vind","t8":"Hos Green Wind Energy kombineres ny viden om vind med mange rs erfaring. Overbevisningen om at overvinde enhver forhindring er forbundet med begejstring, nr rotorerne drejer lnsomt rundt.","b2":"Netvrk","t9":"Det bedste til dig","t10":"Vi hvder ikke, at vi kan alting bedre. Men vi siger, at vi for vores kunder fr det allerbedste ud af maskiner, teknologier, kontrakter, lovgivning og ud af os selv. Vore team rder over kapaciteterne og kompetencerne til at lede et vindprojekt lige fra identifikation af omrdet over finansiering til fremstilling og ibrugtagning, og til at sikre skrddersyede lsninger samt planlgningssikkerhed. I denne forbindelse hrer repowering, som indebrer fornyelsen af gamle anlg, ogs til blandt vores opgaver.","b3":"Team","t11":"","t12":"","t13":"","t14":"","t15":"","t16":"","t17":"","t18":"","t19":""}

