module.exports = {"t1":"Vores team\nEn for alle. Alle for dig!","t2":"Der er mange gode grunde til at vlge Greenwind, nr det glder projektudvikling. Hvert medlem af vores team er en god grund. Og samlet er vi mere end summen af de enkelte medarbejdere.","team":[{"name":"Martin Khl","job":"Administrerende direktr","bio":"har arbejdet med vindenergi siden 1995 og er fascineret af vindens kraft.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140168/Martin_Kuehl_250_ffdbrw.jpg"},{"name":"Manuel Lasse","job":"Projektudvikling og Ibrugtagning\u2028","bio":"med fokus p det store billede og fremtiden.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140168/Manu_Lasse_250_l31s4h.jpg"},{"name":"Franziska Gbel","job":"Projektudvikling","bio":"med udholdenhed og kreativitet til en succesfuld godkendelse.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140168/Franziska_Goebel_250_ny5ifm.jpg"},{"name":"Christian Kandora","job":"Projektudvikling","bio":"med erfaring og engagement i vindparkens optimale layout.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140168/Christian_Kandora_250_vrjajm.jpg"},{"name":"Anne Pfannes","job":"Projektfinansiering","bio":"med njagtighed og betydning for tal og nye ideer.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140168/Anne_Pfannes_250_j8xlvt.jpg"},{"name":"Dietmar Mahler","job":"Omrdeudvikling","bio":"i personlig kontakt med menneskerne p stedet.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140168/Dietmar_Mahler_250_ndqaze.jpg"},{"name":"Bernd Mller","job":"Omrdeudvikling","bio":"med drivkraft og engagement til at fremme landejernes interesser.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140169/Bernd_Mueller_250_mn6n8i.jpg"},{"name":"Ulrich Mertens","job":"Omrdeudvikling","bio":"for at n mlene med at omdanne energisystemet.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140169/Ulrich_Mertens_250_cn82ak.jpg"},{"name":"Jasmin Horstktter","job":"Bogholderi","bio":"med frisk vind og den rette indstilling.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140169/Jasmin_Horstkoetterr_250_dyfxl5.jpg"},{"name":"Michael Witschas","job":"Bogholderi","bio":"for en bedre fremtid.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140169/Michael_Witschas_250_b5e1xt.jpg"},{"name":"Iris Grtner","job":"Team-assistent","bio":"for effektivitet  i jobbet og i energiudnyttelsen.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140169/Iris_Gaertner_250_vs9vuf.jpg"},{"name":"Ulf von Rahden","job":"Omrdeudvikling","bio":"med vind og energi kan meget ndres.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140169/Ulf_von_Rahden_250_w3sb4g.jpg"},{"name":"Sophie Kolb","job":"Assistent for produktudvikling","bio":"altid nysgerrig og yderst engageret i miljvenlig energi.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505140170/Sophie_Kolb_250_qoyc5w.jpg"}]}

