module.exports = {"t1":"Borgerenergi","t2":"Vi rdgiver og flger lokale initiativer af overbevisning.\n\nSom erfaren serviceudbyder og fair partner str vi til rdighed for alle, der ikke kan finde vej i godkendelsesjunglen, ikke kan komme videre p halvvejen, mister pusten under projektudviklingsmaratonen eller nsker at g en enklere vej i deres projektudvikling af vindenergianlg.\n\nVi ved mere om de forskellige finansielle andelsmodeller, om mulighederne for bredygtig naturbeskyttelse og udligningsforanstaltninger, om effektiviteten ved de forskellige teknologier eller den bedste forretningsmssige og tekniske driftsledelse.\n\nTal med os!","t3":"Windflchen","t4":"Udnyt\ndin jord ...","t5":"... med vindens kraft","t6":"Om det er en grsmark, agerjord eller skov - der er passende lsninger til nsten alle udpegede vindmllesteder. Den sknsomme opbygning og den tekniske videreudvikling af hjmoderne anlg gr mange steder lukrative. Vores planlggere kender mulighederne, rdgiver landejere, kommuner og borgere og ved alt om bygge- og afstandsomrder, samt om kabellednings- og anlgsrettigheder.\n\nEt vindenergianlg p din ejendom kan give dig et finansielt perspektiv og yde et bidrag til beskyttelse af klimaet.","sections1":[{"title":"Diverse anvendelsesomrder","p":"Til installeringen af et vindenergianlg er der brug for forskellige omrder, i frste omgang selvflgelig omrdet til at bygge anlgget. Over det hele er der brug for de skaldte afstandsarealer, hvis strrelse er fastlagt i lovgivningen. Desuden er der behov for jord til kabellednings- og anlgsrettigheder. Hvis du derfor gerne vil undersge en mulig arealanvendelse, rdgiver vi dig gerne."},{"title":"Indtgtskilde for mange","p":"Ved enhver form for arealanvendelse modtager landejeren - uanset om han er enkeltperson eller i et partnerskab - sikre vederlag p lang sigt. Vi vlger sammen med dig den mest hensigtsmssige forpagtningsmodel. Ogs naboer kan drage fordel heraf, hvis deres jord ligeledes kommer i betragtning til arealanvendelse. Kommunen opnr desuden indtgter fra erhvervsskatter, som operatren skal betale efter ibrugtagningen af anlgget/anlggene."},{"title":"Vindmllesteder i skoven","p":"Uanset om det er en grsmark, agerjord eller skov - i dag er der passende lsninger til (nsten) alle egnede vindmllesteder. Den sknsomme opbygning og den tekniske videreudvikling af hjmoderne anlg gr mange steder, som tidligere ikke kom i betragtning, mulige og lukrative."}],"t9":"Projektudvikling","t10":"Kloge kommuner, borgere, institutioner, landejere og investorer forstr fordelene ved decentral forsyning med vindkraft. Green Wind Energy udvikler vindparker i regionen i eget regi og sammen med partnere - til fordel for mange. Vore team rder over kapaciteterne og kompetencerne til at lede et vindprojekt lige fra identifikation af omrdet over finansiering til fremstilling og ibrugtagning, og til at sikre skrddersyede lsninger samt planlgningssikkerhed. I denne forbindelse hrer repowering, som indebrer fornyelsen af gamle anlg, ogs til blandt vores opgaver.","sections2":[{"title":"Planlgning og koordinering","p":"I forlngelse af en omrdesikring overtager vi alle planlgnings- og koordineringsprocesser med kommunen og regionsplanlgningen. Vi taler og forhandler med institutioner og investorer, tager os af godkendelser, udtalelser, fremstillingen, netforbindelsen samt den efterflgende driftsledelse af vindenergianlgget.\n\nDet er vores ml at skabe en win-win-situation til gavn for alle parter. Frst da opstr der en accept, som er afgrende for en velfungerende drift. Dertil hrer optimale (brugs-)kontrakter, fornuftige finansierings- og forpagtningsmodeller, tilpassede kompenserende projekter samt natur- og artsbeskyttelsestiltag.\n\nRealiseringen af et vindprojekt er en kompleks opgave. Det varer noget tid, fr rotorbladene drejer lnsomt rundt. Som fremtidig operatr af vindparker vil du altid have en kontaktperson, som gennemfrer processerne transparent og bent, og som rdgiver dig proaktivt og mlrettet."},{"title":"Til gavn for alle","p":"Mange drager fordel af et miljbevidst og sikkert projekteret vindenergianlg: Investorer, som gerne vil opn lukrative afkast med en fornuftig investering, kommuner, som med indtgter fra erhvervsskatter kan forpligte sig i nye projekter, eller ogs landejere, som bortforpagter deres jord til fremstillingen af et anlg.\n\nVores konomiansvarlige kontrollerer ved begyndelsen af enhver projektering, om det kan betale sig at realisere. Plideligt forventede indtgter fra vederlag str over for udfrlige omkostningsberegninger, som indeholder optimerede vedligeholdelses- og forpagtningskontrakter, driftsomkostninger og direkte levering. Vi udvikler skrddersyede finansieringsmodeller og bringer udvalgte partnere, institutioner og banker sammen p det rigtige tidspunkt."},{"title":"One stop supplier","p":"Opbygningen og driften af en vindpark, samt af et enkelt vindenergianlg, krver planlgning og logistik p hjeste niveau og skal g hnd i hnd med de pgldende producenter og tjenesteydere. Den nglefrdige realisering indeholder udvalget af anlg samt fremstillingen, ibrugtagningen og ikke mindst nettilslutningen. For vindenergianlg er kraftvrker. De skal opfylde alle krav om nettilslutning og integrering i forsyningsnettet. Det er ogs vores opgave at gennemfre dette i henhold til alle lovbestemmelser i den tyske lov om fremme af vedvarende energi (EEG), i overensstemmelse med krav fra netoperatren, producenten og operatren, samt med henblik p fremtidige netkoncepter."}],"t11":"Projektindkb","t12":"Green Wind Energy er interesseret i alle vindprojekter p alle udviklingsstadier, ikke kun som rdgiver, men ogs som aktiv og fair partner. Vi tilbyder dig et tillidsfuldt samarbejde. Vi byder ind med alle vores kompetencer, erfaringer og al vores energi for at n et godt og udbytterigt resultat for alle parter.","sections3":[{"title":"Projekter inden idrifttagning","p":"Hvad enten du er jordbesidder eller projektudvikler, kan vi kontrollere og/eller overtage dit projekt i mange forskellige faser, f.eks. nr\n\n alle arealer er sikret,\n alle ansgninger er indsendt,\n ansgningerne str umiddelbart foran godkendelse\n\nog grundlggende, hvis projektudviklingen  af en eller anden grund  ikke har kunnet afsluttes ordentligt."},{"title":"Projekter i drift","p":"Hvis du som driftsansvarlig eller investor har gamle vindenergianlg i din porteflje, kan vi undersge og/eller overtage dine unskede projekter, f.eks.\n\n som ejer eller som delvis ejer,\n i mange forskellige andele,\n med eller uden repowering-potentiale,\n i mange forskellige strrelser,\n\nog grundlggende, hvis du  af en eller anden grund  overvejer at trkke dig ud af dit projekt.\n\nExit: Slip af med gamle investeringer!","link":"repowering"}],"t13":"Repowering","t14":"... fra gammelt til nyt","t15":"Den teknologiske udvikling af vindenergianlg har i de seneste r gjort enorme fremskridt - bde hvad angr effektivitet, bredygtighed og muligheden for at anvende mange forskellige vindmllesteder bedst muligt. Repowering kan her hurtigt tjene sig ind. Green Wind Energy styrer proaktivt og mlrettet processer for repowering.","t16":"Kontakt:","t17":"repowering@greenwindenergy.de","sections4":[{"title":"Forlngelse af lbetiden","p":"For mange vindprojekter, som blev opbygget for mere end ti r siden, betaler det sig i dag med repowering - hvilket indebrer, at et ldre og mindre anlg erstattes med et nyt. Lbetiden for et eksisterende projekt kan dermed forlnges med yderligere 20 r og effektiviteten ges med det tre- til femdobbelte."},{"title":"Kan repowering betale sig?","p":"Om repowering rent faktisk betaler sig, kortlgger vi i begyndelsen af processen. Fr nr alle oplysninger ligger p bordet, skal operatren beslutte sig. Vi hjlper s med at gennemfre repowering-tiltaget - lige fra forundersgelsen og godkendelsesprocedurerne over gennemfrelsen til driften - samt den tekniske og konomiske driftsledelse, som udfres af vores partnervirksomhed Green Wind Operations."}],"t22":"","t23":"","t24":"","t25":"","t26":"","t27":"","t28":"","t29":"","t30":"","t31":"","t32":""}

