module.exports = {"t1":"Effektivitet og engagement p stedet","t2":"Green Wind Energy udvikler lokale vindenergiprojekter i og for regionen. Vi er kontaktpersonen, nr det drejer sig om ttheden i eksisterende parker, repowering eller ogs om nyudviklinger med samarbejdspartnere. Se her et af de strste repoweringprojekter 2017 i Tyskland og andre eksempler p projekter i Brandenburg.","sections":[{"title":"Windpark repowering","subsections":[{"title":"Projekt Vormark/Prignitz (Gro Pankow)","text":"I slutningen af 2016 afmonterede Green Wind Energy 14 eksisterende gamle anlg i det egnede vindomrde. P samme sted blev der opfrt 14 nye vindenergianlg, der blev taget i drift i frste kvartal 2017, inkl. egen transformerstation i Kuhsdorf til tilslutning af vindenergianlggene.\n\nNominel effekt 48,3 megawatt\n\nOpfrelsen af de nye vindenergianlg med en navhjde p 137 meter og en nominel effekt p 3.450 kilowatt (tidligere 1.500 kilowatt) blev udfrt inden for tre mneder. Ud af en nominel effekt p 21 megawatt opstod der dermed 48,3 megawatt. Energiudbyttet bliver ca. 4 gange strre i kraft af repowering. Vindparken er udstyret med passende natmrkning.\n\nUdligningsforanstaltninger\n\nSom udligningsforanstaltninger blev der efter nske fra kommunen revet bygninger ned, der ikke mere var ndvendige, der blev plantet trer og anlagt frugtplantager. Markfirben blev flyttet til nye habitater. P tre steder fik flagermusene nye kasser, og der blev hngt reder op til svalerne.\n\nProjektpartner: European Energy\nTeknisk driftsledelse: Green Wind Operations","img":"http://res.cloudinary.com/greenwind/image/upload/c_scale,w_800/v1505147660/GWE_Referenzen_037C9H1B2821_tujamd.jpg","video":"https://player.vimeo.com/video/233281688?color=000000&title=0&byline=0&portrait=0"},{"title":"Projekt Timpberg","text":"Sted: Zehdenick, Prignitz-Oberhavel, Brandenburg\nAnlg gamle: 2 x VESTAS V80, 4 MW kapacitet, 78 m navhjde, ibrugtagning i 2002\nNedlggelse: 08/2015\nAnlg nye: 2 x VESTAS V112, 6 MW kapacitet, 125 m navhjde, ibrugtagning 12/2015\nBest: profitabel exit for ejeren af de gamle vindenergianlg\u2028attraktivt projekt for den nye operatr\u2028vedvarende sttte fra befolkningen\u2028Green Wind Energy som totalentreprenr","img":""}]},{"title":"Vindpark komprimering","subsections":[{"title":"Projekt Vormark/Prignitz (Gro Pankow)","text":"Frste udvidelse.\nSted: Gro Pankow, Prignitz-Oberhavel, Brandenburg\nAnlg: 2 x VESTAS V80, 4 MW kapacitet, 78 m\nnavhjde\nIbrugtagning: Frste del af den trinvise omlgning af hele vindparken (se ovenfor)\nBest: strk privat og offentlig sttte, Green Wind Energy som totalentreprenr","img":""}]},{"title":"Samarbejde nyudvikling","subsections":[{"title":"Projekt Klein-Mutz","text":"Sted: Zehdenick, Prignitz-Oberhavel, Brandenburg\nAnlg: 4 x VESTAS V90, 8 MW kapacitet, 125 m navhjde\nIbrugtagning: 12/2012\n\nProjektpartner: Saxovent, Notus\nBest: konstruktivt samarbejde, kort projektlbetid til godkendelse og gennemfrelse","img":"http://res.cloudinary.com/greenwind/image/upload/c_scale,q_50,w_1052/v1505994982/AIMG_0794_gr_x1eggg.jpg"}]}]}

