module.exports = {"t1":"Med vindens kraft","t2":"... arbejder vi p, at energi, der udvindes p havet, strmmer effektivt, problemfrit og konomisk i land. S den er tilgngelig for alle! Hvert medlem af vores team stiller store krav til sin profession og gr til arbejdet med fuld kraft. Det gr os strke!","t3":"Manuel Lasse, diplomingenir (FH)\ninden for miljteknologi og vedvarende energi","t4":"administrerende direktr\nshar arbejdet for fuld kraft med vindenergi siden 2006.","t5":"Green Wind Offshore","t6":"Green Wind Offshore GmbH er en virksomhed inden for Green Wind Gruppe med hovedsde i Berlin centrum. Som specialist inden for vedligeholdelsesstyring og overvgning af tekniske anlg til energiproduktion offshore er vi ogs aktive i Rostock og i omrdet ved Emden/Wilhelmshaven.\n\nVi opererer selvstndigt og uafhngigt p hele det tyske og det internationale marked. Vi yder fuld sttte inden for teknisk driftsledelse af offshore-vindenergianlg og inden for platformsstyring til HVDC og transformerstationer. Vi arbejder med fokus p samarbejde og partnerskab og har i mange r vret fast forankret i vindenergien.\n  Certificeret iht. ISO 9001:2015.","b1":"Ydelser","t7":"Ny viden om vind","t8":"Green Wind Offshore er ny viden om vinden kombineret med erfaring og overbevisningen om at komme med p enhver blge. Det er begejstring, nr et hjul passer ind i de andre, nr planlgning, handling og organisering problemfrit samles til et stort hele, og hele indsatsen bliver til ren energi.","b2":"Netvrk","t9":"Det bedste til dig","t10":"Vi hvder ikke, at vi kan alting bedre. Men vi siger, at vi for vores kunder fr det allerbedste ud af maskiner, materialer, teknologier, kontrakter, lovgivning og ud af os selv. Vi har et fintmasket netvrk og en lokal tilstedevrelse i aktiviteterne i Nord- og sters-regionen. Vores kunder kan regne med korte veje og en direkte kontaktperson.","b3":"Team"}

