module.exports = {"t1":"Vores team\nFuld kraft frem!","t2":"Der er mange gode grunde til at beslutte sig for Green Wind Offshore, nr det drejer sig om teknisk styring. Hvert medlem af vores team er en god grund. Og samlet er vi mere end summen af de enkelte medarbejdere. Vores motto: Fuld kraft frem!","team":[{"name":"Manuel Lasse","job":"administrerende direktr","bio":"Har arbejdet for fuld kraft med vindenergi siden 2006.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505142196/Manu_Lasse_250_wmmhuo.jpg"},{"name":"Peter-Heinrich Boysen","job":"Teknisk direktr","bio":"Mlbevidst bde til lands og til vands.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505142196/Peter_Heinrich_Boysen_250_k7xei3.jpg"},{"name":"Andreas Texheimer","job":"Kvalitetschef","bio":"med njagtighed og fokus p safety first.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505141768/Andreas_Texheimer_250_nzbrbq.jpg"},{"name":"Rico Kaatz","job":"Projektingenir CMMS","bio":"vedvarende p farten.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505142196/Rico_Kaatz_250_jxgxfh.jpg"},{"name":"Fabio da Fonseca Bola","job":"Projektingenir CMMS","bio":"Ny viden om vind direkte fra universitetet.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505142196/Fabio_da_Fonseca_Bola_250_cy5eo9.jpg"}]}

