module.exports = {"t1":"Hovedkontrolcenter","t2":"Effektiv og koordineret overvgning.","t2b":"Hele projektledelsen for store offshore-projekter befinder sig for det meste i vores kontrolcentre 24/7.","sections0":[{"title":"Kontrolcentre tt p","p":"Hovedkontrolcentret og hjertet ligger i Berlin. Herfra indstter vi kontrolcentre i umiddelbar nrhed af projekternes placering, som f.eks. ved vedligeholdelsesstyringen af nettilslutningssystemet DolWin3. Hvad enten det drejer sig om bortskaffelse af affald og spildevand, reservedelsforsyning og -styring eller Technical Support Line (TSL)  her dokumenterer, systematiserer, overvger og kontrollerer vores eksperter hele anlgsdriften og -tilstanden."},{"title":"Overvgning dgnet rundt","p":"P den mde kan alle indsatsgrupper og deres aktiviteter styres optimalt og forsynes via den eksisterende lagerlogistik med de ndvendige reservedele og de relevante vrktjer. Alle de vigtigste processer overvges og koordineres centralt  24 timer i dgnet, syv dage om ugen. Effektivt og sikkert."}],"t3":"","t4":"Planlaegning","t5":"Nye koncepter udtnkes.","t6":"Udgangspunktet for vores tekniske styring er devisen: Maksimer driftstiden, og nedst omkostningerne.\n\nVedligeholdelsesstyring og teknisk driftsledelse har en stor betydning for, hvor stor den konomiske succes for offshore-havmlleparker bliver i fremtiden.\n\nVores sofistikerede koncept starter med en omhyggelig forberedelsesfase. I koordination med ordregivere, vrfter, konstruktrer og ingenirer skaber vi sikkerhed i planlgningen.","sections1":[{"title":"Fremadskuende og systematisk","p":"Med til vores styrker hrer en fleksibel og fremadskuende tankegang, prcision i planlgningen, oversigt over kampagner og strukturer, fuldstndig dokumentation for materialer og processer, systematisk sttte til vedligeholdelsesprocesser via anvendelse af software (CMMS)."},{"title":"For alle store offshore-projekter","p":"Det af Green Wind Offshore nyudviklede vedligeholdelseskoncept for vindenergianlg, platforme, kabler til lands og til vands, transformerstationer og koblingsudstyr kan overfres til store projekter af samme type i Nord- og stersen og langs de engelske og franske kyster."}],"t9":"Management","t10":"","sections2":[{"title":"Hurtig og direkte handling.","p":"Vores mde at handle p er bestemt af korte reaktionstider og mlet om at undg stilstand og ge effektiviteten.\n\nBrugen af den nyeste teknik giver maksimal tilgngelighed for anlg og de hjeste sikkerhedsstandarder for personer og anlg. En certificeret offshore-uddannelse af vores medarbejdere er en selvflge. Hj motivation og engagement ger kvaliteten. Hertil kommer ogs: flade hierarkier og hurtige beslutningsprocesser samt samarbejdsvillighed og fokus p partnerskab. Store offshore-projekter har ikke brug for individualister, men for frsteklasses teamplayere."},{"title":"Til lands og til vands","p":"Med til vores mde at handle p hrer ogs organisering og koordinering af alle vedligeholdelsesforanstaltninger, der kan planlgges, ikke-planlagt vedligeholdelsesarbejde og fejlafhjlpning samt yderligere vedligeholdelsesforanstaltninger til lands og til vands."},{"title":"Kontrolcentre og logistikbaser","p":"Takket vre vores kontrolcentre, logistikbaser, det fleksible personale og de effektive partnere har Green Wind Offshore muligheden for at integrere yderligere HVDC-platforme, offshore-transformerstationer og offshore-vindenergianlg i eksisterende vedligeholdelsesstrukturer og evt. for at udvide dem. Vi integrerer enkelte systemer og aflaster dermed vores kunder."},{"title":"Managementsystemer","p":"Vores vigtigste ml inden for kvalitetsstyring er en struktureret intern organisation samt et kooperativt og vellykket samarbejde med kunder og leverandrer. Dette omfatter ligeledes overholdelse af tidsfrister og budgetter. Men ogs omrderne sikkerhed, arbejdsbeskyttelse og milj er underlagt den kontinuerlige kontrol. Vi arbejder kontinuerligt p alle systemer, som","list":["Kvalitetsstyringssystem (QMS) iht. ISO 9001","IT-sikkerhedsstyringssystem iht. ISO 27001","Miljstyringssystem (UMS) iht. ISO 14001","Asset-styringssystem iht. ISO 55001","Arbejdsbeskyttelsesstyringssystem og HSE osv. integreret i 9001 (QMS) og 14001 (UMS) samt OHSAS ISO 18001","Energistyringssystem iht. ISO 50001"]}],"t11":"Logistik","t12":"","sections3":[{"title":"Fleksibel og prcis organisering.","p":"Med henblik p hj tilgngelighed og optimerede arbejdsprocesser skal logistik og reservedelsforsyning i den tekniske styring altid stemme."},{"title":"Kystnre placeringer","p":"P havet kan blot n manglende skrue betyde undvendigt besvr. For at reservedelsstyringen kan klare bde forsyning og bortskaffelse af forbrugs- og driftsstoffer s hurtigt som muligt, installerer og administrerer vi kystnre lokaliteter inklusive reservedelslager med manpower og via CMMS. Den sikre offshore-transport af materialer og personer og anbringelsen af dem hrer ligeledes med til vores opgaver ligesom transport fra oplagringssteder til omskibningshavne og transformerstationer p land."},{"title":"Forebyggende og korrigerende","p":"Alle store projekter har deres egne srlige krav, som vi reagerer fleksibelt p. Forebyggende og korrigerende vedligeholdelseskampagner krver en prcis indsats fra teknikere til lands og til vands."}]}

