module.exports = {"t1":"Nettilslutningssystem DolWin3  best practice 2023.","t2":"Som den ene af to partnere i ARGE O+M-projektet DolWin3 har Green Wind Offshore i 2017 overtaget vedligeholdelsesstyringen af nettilslutningssystemet DolWin3. Delen af det system, der skal overvges, er den 18.450 tons tunge konverterplatform 'DolWin Gamma', der er opfrt i Nordsen i 2017  50 kilometer nord for Borkum  p den eksisterende underkonstruktion.","sections":[{"title":"","subsections":[{"title":"900 megawatt-jvnstrmsforbindelse","text":"Den 85 meter lange, 54 meter brede og 32 meter hje konverterplatform blev bugseret fra vrftet i Rostock gennem stersen, rundt om Danmark og ind i den eksklusive konomiske zone i den sydvestlige del af det tyske Nords-omrde (foto). Nettilslutningssystemet DolWin3 er en 900 megawatt-jvnstrmsforbindelse. Systemet udgr forbindelsen mellem to offshore-vindmlleparker i DolWin-clusteret og 400 kilovolt-transformerstationen i Drpen West p land.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505153569/GWOf_Referenzen_Bild_klein_Karte_web_mt7vxk.jpg"},{"title":"Eget vedligeholdelseskoncept","text":"Den samlede vedligeholdelseskontrakt bestr af en forberedelsestid p flere mneder samt en efterflgende femrig vedligeholdelsesfase og omfatter bde vedligeholdelsesaktiviteter p konverterstationen 'DolWin Gamma' og transformerstationen Drpen West p land. ARGE er indtil 2023 ansvarlig for al planlgning, organisation og gennemfrelse af vedligeholdelsen p basis af deres egenudviklede vedligeholdelseskoncept.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505153602/GWOf_Referenzen_Bild_klein_Boysen_web_k5iepr.jpg"}]}]}

