module.exports = {"t1":"Med vindens kraft","t2":"... finder vi lsninger til de fleste problemer og tager alt op med optimisme, som drejer rundt eller (endnu) ikke drejer. Vi lber ikke tr for nye ideer. Mit team og jeg har gentagne gange bevist, at vi overvinder (nsten) alle forhindringer og kommer videre som mange andre. Vindenergi - det er mere for mig end blot et job.","t3":"Manuel Lasse","t4":"Administrerende direktr\nDiplomingenir (FH) inden for miljteknologi og vedvarende energi, har arbejdet med vindenergi siden 2006","t5":"Green Wind Operations","t6":"... er en vindenergivirksomhed med hovedsde i Berlin centrum og projekter i hele Tyskland. Vi opererer selvstndigt og uafhngigt. Vi giver fuld sttte til teknisk og konomisk driftsledelse til private og institutionelle operatrer af vindparker. Vi arbejder med fokus p samarbejde og partnerskab og har i mange r vret fast forankret i vindenergien. Certificeret iht. ISO 9001:2015.","b1":"Ydelser","t7":"Ny viden om vind","t8":"Hos Green Wind Operations kombineres ny viden om vind med mange rs erfaring. Overbevisningen om at overvinde enhver forhindring er forbundet med begejstring, nr rotorerne drejer lnsomt rundt.","b2":"Netvrk","t9":"Det bedste til dig","t10":"Vi hvder ikke, at vi kan alting bedre. Men vi siger, at vi for vores kunder fr det allerbedste ud af maskiner, teknologier, kontrakter, lovgivning og ud af os selv. Vores team rder over kapaciteterne og kompetencerne til dgnet rundt at lede vindenergianlg bedst muligt, efter de er taget i brug.","b3":"Team","t11":"Flger udviklingen med at omdanne energisystemet","t12":"Vi er altid velinformeret takket vre vores sagkyndige medarbejdere og vores placering i Berlin - direkte i det politiske centrum for omdannelsen af energisystemet. Om det er repowering, handel med elektricitet, lovmssige grundlag, opdateringer eller fornyelser til stabilisering af forsyningsnettet inden for omrdet af den tyske lov EEG om fremme af vedvarende energi - vi rdgiver dig aktivt og giver vores anbefalinger.","b4":"Kontakt","t13":"","t14":"","t15":"","t16":"","t17":"","t18":"","t19":""}

