module.exports = {"t1":"Vores team\nEn for alle. Alle for dig!","t2":"Der er mange gode grunde til at vlge Greenwind, nr det glder konomisk og teknisk driftsledelse. Hvert medlem af vores team er en god grund. Og samlet er vi mere end summen af de enkelte medarbejdere.","team":[{"name":"Manuel Lasse","job":"Administrerende direktr","bio":"har studeret miljteknologi og vedvarende energi samt arbejdet med vindenergi siden 2006.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505141767/Manu_Lasse_250_nvxkgv.jpg"},{"name":"Andreas Texheimer","job":"Teknisk og konomisk driftsledelse","bio":"med njagtighed og fokus p safety first.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505141768/Andreas_Texheimer_250_nzbrbq.jpg"},{"name":"Dietmar Mahler","job":"Teknisk driftsledelse","bio":"i personlig kontakt med menneskerne p stedet.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505141768/Dietmar_Mahler_250_ljvlzb.jpg"},{"name":"Franziska Brll","job":"Teknisk og konomisk driftsledelse","bio":"med fokus p det store billede.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505141767/Franziska_Bruell_250_qeonht.jpg"},{"name":"Anne Pfannes","job":"konomisk driftsledelse","bio":"med njagtighed og betydning for tal og nye ideer.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505141768/Anne_Pfannes_250_woqu6v.jpg"},{"name":"Max Horn","job":"Teknisk driftsledelse","bio":"med begejstring for vedvarende energi.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505141768/Max_Horn_250_vynxjk.jpg"},{"name":"Robert Kadereit","job":"Teknisk driftsledelse/styreenhed","bio":"er hurtigt p stedet.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505141769/Robert_Kadereit_250_n1shkm.jpg"},{"name":"Jasmin Horstktter","job":"Bogholderi","bio":"med frisk vind og den rette indstilling.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505141769/Jasmin_Horstkoetterr_250_i9fwoh.jpg"},{"name":"Philipp Grytz","job":"teknisk driftsledelse, regionalkontoret Gro Pankow","bio":"altid p forkant med den tekniske udvikling.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505141768/Philipp_Grytz_250_feghaw.jpg"},{"name":"Michael Walther-Schwartz","job":"teknisk driftsledelse, regionalkontoret Gro Pankow","bio":"klar til enhver udfordring.","img":"http://res.cloudinary.com/greenwind/image/upload/v1505141768/Michael_Witschas_250_mmlrqh.jpg"}]}

