module.exports = {"t1":"Hovedkontrolcenter dgnet rundt","t2":"Alle processer inden for den tekniske driftsledelse af vindenergianlg og transformerstationer vises hovedsageligt i vores kontrolcentre 24/7. Hovedkontrolcentret og hjertet ligger i Berlin. Vi dokumenterer, systematiserer, overvger og kontrollerer anlggenes samlede drift og tilstand. P den mde kan vores indsatsgrupper og deres aktiviteter styres optimalt, ligesom de effektivt kan vre til stede, hvor der er brug for det. Alle de vigtigste processer overvges og koordineres centralt  24 timer i dgnet, syv dage om ugen.\n\nEffektivt og sikkert.","t3":"","t4":"Teknisk driftsledelse","t5":"","t6":"","sections1":[{"title":"Kontrol af forlb","p":"Vi besigtiger regelmssigt anlggene - lige fra kabinen til maskinhuset - og kontrollerer og dokumenterer minutist, om kontrolintervaller og vedligeholdelsesopgaver overholdes. Dertil hrer ogs den halvrlige inspektion af hele vindenergianlgget. Vores kunder er altid fuldt informerede om alle processer takket vre kundens egen webadgang, individuel udarbejdelse af rapporter og personlig rapportering."},{"title":"Helt prcis","p":"Frst nr det tekniske arbejde p stedet, analysen af data, udtalelser og besigtigelser, indhentningen af tilbud samt den detaljerede undersgelse af et objekt bliver samlet og sagkyndigt forbundet, opnr vi optimale resultater. Frst da udvikler teknisk driftsledelse sin fulde kraft. Derfor koordinerer Green Wind Operations ogs eksterne driftsledere i hele Tyskland og i nabolandene, skaber flles strukturer og samler alle informationsstrmme."},{"title":"Overvget dgnet rundt","p":"Til dataovervgningen bruger vi det kendte program ROTORSOFT fra firmaet Drehpunkt samt softwaren WIS fra firmaet Softenergy, dvs.:\n\n 24 timers live-overvgning af vindenergianlggene\n Software-baseret grnseflade til den konomiske driftsledelse\n ideelle betingelser til handel med elektricitet\n\nDen individuelle udarbejdelse af rapporter er tilpasset din porteflje med mneds- og rsrapporter. Derudover udarbejder vi konomiske driftsledelsesrapporter."},{"title":"Overvgning af eksterne ydelser","p":"Tillid er godt, kontrol er bedre! Eksterne tjenesteyderes arbejde bliver kontrolleret lbende, og kontraktmssige aftaler og afregninger bliver gennemget. Vi organiserer og gennemfrer supplerende bestemmelser i henhold til den tyske lov om beskyttelse mod emissioner 'BiMSchG'"}],"t9":"konomisk driftsledelse","t10":"konomisk driftsledelse hos Green Wind vil sige at sikre altid strmmende finansielle bevgelser og udelukke alle forstyrrende faktorer og undvendige omkostninger for at opn det bedst mulige afkast. Vi kan allerede p forhnd kontrollere hele portefljen for s vidt angr eksisterende kontrakter og komme med forslag til en fornuftig kontraktstruktur, f.eks. til optimering af vedligeholdelseskontrakter.","sections2":[{"title":"Optimering af oplysninger","p":"Selve kernen i en konomisk driftsledelse er et klippefast bogholderi og herunder afviklingen af betalinger. Desuden optimerer vores konomiansvarlige den regnskabsmssige proces, sledes at den er skrddersyet til driften af hver enkelt anlg, tilpasset de specifikke finansieringsstrukturer og de skattemssige faktorer. Vores medarbejdere beregner den bedste effektivitet for anlgget og tager sig af en gnidningsfri afregning samt fordeling af indtgter fra leveringer."},{"title":"Planlgning og styring af processer","p":"Til planlgning og styring af disse processer anvender vi vores nye viden om vind, hvad angr likviditets- og budgetplanlgning, afregning af driftsforstyrrelser i netsupporten, direkte levering, commercial due diligence, etc. Og alt er baseret p den lbende kontakt til kunden."}],"t11":"Repowering eller optimering","t12":"Den teknologiske udvikling af vindenergianlg har i de seneste r gjort enorme fremskridt - bde hvad angr effektivitet, bredygtighed og muligheden for at anvende mange forskellige vindmllesteder bedst muligt. Vores driftsledere kender anlggene fuldt ud og laver forslag til driftsoptimeringer eller ogs repowering-tiltag.","sections3":[{"title":"Yderligere 20 rs lbetid","p":"For mange vindprojekter, som blev opbygget for mere end ti r siden, betaler det sig med repowering - hvilket indebrer, at et ldre og mindre anlg erstattes med et nyt. Lbetiden for et eksisterende projekt kan dermed forlnges med yderligere 20 r og effektiviteten ges med det tre- til femdobbelte. S kan en investering hurtigt tjene sig ind."},{"title":"Kan repowering betale sig?","p":"Vi betragter det som vores opgave, at vi til vores kunder proaktivt og aktivt laver forslag til repowering og p kompetent vis kortlgger, om repowering rent faktisk betaler sig. Fr nr alle oplysninger ligger p bordet, skal operatren beslutte sig."},{"title":"Projektering med partner","p":"Vores partnervirksomhed Green Wind Energy GmbH hjlper med at gennemfre repowering-tiltag - lige fra forundersgelse og godkendelsesprocedurer over gennemfrelse til drift. Green Wind Energy har i flere r udviklet vindparker i regionen sammen med partnere og styrer processer for repowering. Hvis det nskes, kan Green Wind Operations GmbH derefter gnidningslst overtage den tekniske og konomiske driftsledelse med den sdvanlige kvalitet."}]}

