module.exports = {"t1":"Viden om vind","t2":"Vi samler viden om vind til dig: aktuelle nyheder fra branchen, ny lovgivning, aspekter af politiske diskussioner, forbundsnyheder eller ogs interessante links til vindtemaer. Hvis du vil vide mere, beder vi dig kontakte os.","sections":[{"title":"HUSUM Wind","subtitle":" siden vindenergimessen Husumer Windenergietage og frem til i dag","text":"Mens der endnu ikke var mange i verden, der tog vindenergi alvorligt som konomisk faktor, blev den frste vindenergimesse Husumer Windenergietage afholdt i 1989 - i en kvgauktionshal. Det viste sig hurtigt, at byen Husum ved det forblste Nordsen ikke kun har potentiale som frende vindmllested, men ogs som messeby. I 1997 blev der frdiggjort en ny messehal, og i de flgende r blev dens infrastruktur lbende forbedret. I august 2010 bnede s det moderne og multifunktionelt udstyrede kongrescenter NordseeCongressCentrum, som med stor succes blev anvendt til mder og kongresser for frste gang p HUSUM WindEnergy 2010. Og messepladsen fortstter med at vokse.","link":{"text":"Se mere p","url":"www.husumwind.com"},"more":"Green Wind Operations GmbH er med fra den 15. - 18. september 2015 og glder os til dit besg."},{"title":"Nye bestemmelser for driftsledelse","subtitle":"","text":"Systemets stabilitet:\n Generelt\n den tyske forordning til sikring af systemets stabilitet 'SysStabV' (siden marts 2015)\n\nKontrol af serviceelevatorer: Generelt den nye tyske forordning om driftssikkerhed 'BetrSichV' (i kraft siden 1. juni 2015)\n\nISDN og analoge tilslutninger: Telekom meddeler afvikling til 2016/2017.\n\nFjernstyringsanlg: Udveksling gennem netoperatr (tysk lov om fremme af vedvarende energi EEG 2014,  9, stk. 1)"},{"title":"Strm fra vind","subtitle":"","text":"Elproduktion fra vindkraft er inden for f r steget markant. I 2014 producerede 24.867 vindenergianlg i Tyskland med en installeret kapacitet p godt 38.115 megawatt ren strm til virksomheder og husholdninger. Dermed dkker vindenergien i dag 8,9 procent af det samlede bruttoenergiforbrug og leverer det strste bidrag til elproduktion fra vedvarende energi. Mere end 137.000 mennesker er beskftiget i vindenergibranchen. Vindenergiens potentiale er endnu ikke udtmt...","link":{"text":"Se mere p","url":"www.unendlich-viel-energie.de"}},{"title":"Blinker, nr der kommer et fly","subtitle":"","text":"I fremtiden vil det vre tilladt, at de blinkende markeringslygter p anlggene kun tndes, nr luftfartjer nrmer sig. Den tilsvarende ndring af den administrative bestemmelse blev vedtaget i det tyske forbundsrd den 10. juli 2015. Heri blev det fastsat at godkende nye lysteknologier, at indfre vre grnser for lysstyrke, at reducere minimumsantallet af niveauer for hindringslys ved belysning af vindenergianlg, samt at bne muligheden for at aktivere natafmrkning af vindenergianlg efter behov.\n\nI Tyskland skal vindenergianlg med en hjde p mere end 100 meter vre markeret med et rdt blinkende lys om natten. (...) Konstante blink kunne dermed, i det mindste ved strre vindparker, hre fortiden til. Det afgrende for succesen ved behovsorienteret markering er systemernes konomi (...)","link":{"text":"Kilde: BWE, 10.7.15, ","url":"www.wind-energie.de"}}]}

