module.exports = {"t1":"Impressum","t2":"Green Wind Operations GmbH\nAlt-Moabit 60a\nD-10555 Berlin\n\nPhone: +49 / (0)30 / 351 28 86 - 30\nFax +49 / (0)30 / 351 28 86 - 33\nE-Mail: berlin@greenwindenergy.de\n\nHRB 118506 B Charlottenburg Byret | Administrerende direktr: Manuel Lasse\n\nOphavsret\nDenne hjemmesides layout og anvendelsen af grafik, ideer og offentliggrelser er ophavsretligt beskyttet. Disse sider m kun kopieres til privat brug, ndringer m ikke foretages, og kopier m ikke videreformidles uden samtykke.\n\nAnsvar\nHjemmesiden er underkastet regelmssig, omhyggelig og indholdsmssig kontrol. Vi ptager os dog ikke ansvaret for indholdet af andre hjemmesider, som links henviser til. Ansvaret for indholdet af de linkede sider phviler udelukkende udbyderen af siderne. Anvendelsen af disse links sker p eget ansvar.\n\nDDesign & Programmering\nHELDISCH.COM\n\nBilleder\nPablo Castagnola\nPeter Zeidler, Z-Entertainment\n\nTekst\nAnke Kuckuck"}

